.titulo-about-me {
  color: rgb(243, 136, 94);
  text-align: center;
  padding-top: 11%;
  font-size: 22px;
}

.imagen-about-me {
  width: 50%;
  border-radius: 10%;
} 

.caja-about-me {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  padding: 2% 0% 2% 0%;
}

.content-text {
  width: 90vw;
}

.text-about-me {
  color: #8D99AE;
  margin-bottom: 5%;
}

.div-button-download-CV {
  text-align: center;
  padding-top: 3%;
}

.button-descargar-cv {
  text-align: center;
  font-size: 16px;
  background-color: #2B2D42;
  color: white;
  border: solid 1px white;
  padding: 5px 40px;
  border-radius: 5px;
  cursor: pointer;
}

.button-descargar-cv:hover {
  background-color: #8D99AE;
  color: white;
}


@media (min-width: 426px) {
  .titulo-about-me {
    padding-top: 9%;
  }
}  

/* @media (min-width: 553px) {
 .imagen-about-me {
    width: 230px;
 } 
}  */

@media (min-width: 680px) {
  .titulo-about-me {
    padding-top: 7%;
    font-size: 24px;
  }

  .text-about-me {
    font-size: 18px;
    margin-bottom: 0%;
  }

  .button-descargar-cv {
    font-size: 18px;
  }

  .content-text {
    width: 45vw;
  }

  .imagen-about-me {
    width: 300px;
    border-radius: 10%;
  } 

  .div-button-download-CV {
    padding-top: 0;
  }
} 

@media (min-width: 1024px) {
  .titulo-about-me {
    padding-top: 4.5%;
  }
} 
